<template>
	<view>
<!-- 		<u-navbar :is-back="false" title="消息中心" :is-fixed="false" :background="background">
			<view class="navbar-right" slot="right">
				<icon class="iconfont icon-saoma" @click="startScan"></icon>
			</view>
		</u-navbar> -->
		<view class="search_wrap">
			<u-search 
			placeholder="搜索" 
			shape="square" 
			:show-action="false" 
			:clearabled="true" 
			class="search" 
			input-align="left" 
			bg-color="#fff" 
			border-color="#8b8b8b"
			margin="0 15rpx"			
			v-model="result"></u-search>
		</view>
		<view class="cell_wrap">
			<u-cell-group>
				<u-cell-item icon="chat" title="公告" ></u-cell-item>
				<u-cell-item icon="integral-fill" title="预警" value="新版本"></u-cell-item>
				<u-cell-item icon="integral-fill" title="消息" value="新版本"></u-cell-item>
			</u-cell-group>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				background: {
					'background-image': 'white'
				},
				barcode1: null,
				isFlash: false,
				isShow: false,
				result: '',
				keyword: '',

			}
		},
		onLoad() {
			// #ifdef APP-PLUS
			// 微信登录鉴权服务
			var aweixin = null;
			// 当前环境支持的所有登录鉴权服务
			var auths = {};
			// 获取登录鉴权服务列表，单独保存微信登录鉴权服务对象
			// 5+APP在plusready事件中调用，uni-app在vue页面的onLoad中调用
			plus.oauth.getServices(function(services) {
				for (var i in services) {
					auths[services[i].id] = services[i];
				}
				aweixin = auths['weixin'];
				console.log(aweixin);
			}, function(e) {
				plus.nativeUI.alert("获取登录鉴权服务列表失败：" + JSON.stringify(e));
			});
			//#endif
		},
		mounted() {
			if (!window) {
				global.navigator = {
					userAgent: plus.navigator.getUserAgent()
				}
			}
		},

		methods: {
			
			//保存照片到手机
			setPicture(path) {
				// #ifdef APP-PLUS
				plus.gallery.save(path,
					function() {
						console.log("保存成功")
					}, this.onerror()
				)
				// #endif
			}

		}
	}
</script>

